import React, { useState } from 'react'
import { useSelector } from 'react-redux'
import Calculator from 'Component/Calculator'
import { useEffect } from 'react'

const showHandler = (setShowTooltips, tooltipsCheck, show) => {
    if (typeof (setShowTooltips) && tooltipsCheck && show) setShowTooltips(1)
    else setShowTooltips(0)
}
const Pack = ({
    isEmpty,
    entryListIdx,
    poListIdx,
    uom,
    inputWidth,
    values,
    onChange,
    showTitle,
    placeholder,
    title,
    className,
    useCalculator,
    singleView,
    grade,
    table,
    activeTab,
    activeSku,
    withTooltips
}) => {

    const show = uom === 'gram' || uom === 'kg' ? false : true
    const [showCalculator, setShowCalculator] = useState()
    const [showTooltips, setShowTooltips] = useState(0)
    let item = useSelector(state => state.repackEntryData?.jobPost?.list[activeSku]?.poList[poListIdx]?.itemList?.filter(item => item?.pack))
    if (item?.length) item = item[item?.length - 1]

    // if (item.pack === values)
    let remaining = table?.data?.filter(row => row.grade === grade?.label && Number(row.slot) === Number(activeTab))[0]?.repackTargetRemaining
    if (remaining?.length && remaining?.includes('+')) {
        remaining = remaining?.split('+')?.join('')
    }

    useEffect(_ => {
        if (Number(remaining) > 0 && item?.pack === values && item?.grade?.label === grade?.label && Number(item?.slot?.value) === Number(activeTab)) setShowTooltips(1)
        else setShowTooltips(0)

        // setTimeout(_ => setShowTooltips(0), 1000)
    }, [Number(remaining) > 0, values, item?.pack, grade?.label, Number(activeTab)])
    const tooltipsCheck = Number(remaining) > 0 && item?.pack === values && item?.grade?.label === grade?.label && Number(item?.slot?.value) === Number(activeTab)
    return (
        <div className={` ${inputWidth} mb-3 mr-3 ${className} ${show ? null : 'd-none'}`}>
            <div className={` pb-1 LatoBold ${showTitle ? null : 'd-none'}`}>{title}</div>
            <div className={`${isEmpty ? 'border-red' : 'border'} rounded relative border-soft-green w-100 bg-white d-flex align-items-center input-height px-2 mr-3`}>
                <input onMouseOver={() => showHandler(setShowTooltips, tooltipsCheck, true)} onMouseLeave={() => showHandler(setShowTooltips, tooltipsCheck)} onBlur={() => showHandler(setShowTooltips, tooltipsCheck)} onClick={() => setShowCalculator(!showCalculator)} id={`pack_${poListIdx}_${entryListIdx}`} maxLength='10' autoComplete='off' type='text' className='w-100 plain-input' value={values} name='pack' placeholder={placeholder} onChange={(e) => onChange(e)} />
                <div className='font-soft-grey'>{uom}</div>
                <div className={`popover-style ${withTooltips ? null : 'd-none'}`} style={{ opacity: showTooltips, transition: 'ease-in-out opacity 0.2s', transitionDuration: 0.2 }}>
                    <div className='popover-content'>Because Slot {activeTab} is full, the remaining <span className='green'>{remaining}</span> pack will go to next slot automatically</div>
                </div>
            </div>

            <Calculator
                singleView={singleView}
                useCalculator={useCalculator}
                className={showCalculator ? null : 'd-none'}
                setShowCalculator={setShowCalculator}
                showCalculator={showCalculator}
                onChange={(val) => onChange(null, val)}
                name={'pack'}
                entryListIdx={entryListIdx}
                poListIdx={poListIdx} />
        </div >
    )
}

export default Pack